body {
  background: #fff;
  color: #222;
  font-family: 'Segoe UI', Arial, sans-serif;
  margin: 0;
  padding: 0;
}
.app-container {
  max-width: 600px;
  margin: 60px auto;
  padding: 32px;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 2px 16px rgba(0,0,0,0.08);
}
form {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 32px;
}
form > div {
  display: flex;
  align-items: center;
  gap: 16px;
  flex-wrap: wrap;
}
input, select, button {
  font-size: 1rem;
  padding: 8px 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
}
label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 500;
}
input[type="checkbox"] {
  width: 18px;
  height: 18px;
}
button {
  background: #e11d3f;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
}
button:hover {
  background: #b81a32;
}
.mt-4 {
  margin-top: 1rem;
}
.logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2rem;
}
